export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Photos",

  data: () => ({
    photos: [],
    error: null,
  }),

  head() {
    return {
      title: "Photos"
    };
  },

  created() {
    this.$axios.get('https://api.aurelienk.space/photos/get.php').then(result => {
      if (result.data) this.photos = result.data;
    }).catch(error => {
      console.error(error);
      this.error = error;
    })
  }

}
